
#container {
  position: fixed;
  width: 100%;
  height: 100%;
  background: url('assets/bg.jpg') center no-repeat;
  background-size: cover;
}
.page-wraper,
.page {
  width: 100%;
  height: 100%;
}
.page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.logo {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50px;
  margin: auto;
  opacity: 0.5;
  width: 15%;
}



