
@charset "UTF-8";
.swiper-slide span {
  visibility: hidden;
  opacity: 0;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
}
.a2 span {
  -webkit-transform: translateY(180px) rotate(120deg) scale(0);
          transform: translateY(180px) rotate(120deg) scale(0);
}
.a3 span {
  -webkit-transform: skewY(-60deg) translateY(-200px);
          transform: skewY(-60deg) translateY(-200px);
}
.a5 span {
  -webkit-transform: rotate(180deg) scale(0);
          transform: rotate(180deg) scale(0);
}
.a6 span {
  -webkit-transform: scale(0) skewX(60deg) translateY(280px);
          transform: scale(0) skewX(60deg) translateY(280px);
}
.a7 span {
  -webkit-transform: scale(5) translateY(20px);
          transform: scale(5) translateY(20px);
